import GrayScale from './effects/color/GrayScale';
import Sepia from './effects/color/Sepia';
import Contrast from './effects/color/Contrast';
import Brightness from './effects/color/Brightness';

let availableEffects = {};

function add(name, _class) {
    availableEffects[name] = _class;
}

add('gray-scale', GrayScale);
add('sepia', Sepia);
add('contrast', Contrast);
add('brightness', Brightness);

class EffectsRepository {
    static get(name) {
        return availableEffects[name];
    }
}

export default EffectsRepository;


